.wrapper {
  width: 100%;
  height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
}

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  scroll-behavior: smooth;
}

body {
  font-family: var(--font-family);
  background-color: var(--bg);
}

button {
  border: 0;
  background: transparent;
}

a {
  text-decoration: none;
}

@font-face {
  font-family: 'Avenir Next Ultra';
  src: url('../src/assets/fonts/AvenirNext-UltraLight.woff2') format('woff2'), url('../src/assets/fonts/AvenirNext-UltraLight.woff') format('woff');
  font-weight: 200;
  font-style: normal;
  font-display: swap;
}

